import React from "react";
import "./App.css";
import { BrowserRouter, Route } from "react-router-dom";
import NewLanding from "./New/NewLanding";
import About from "./New/About";
import Resume from "./New/Resume";
import Services from "./New/Services";
import Portfolio from "./New/Portfolio";
import PortfolioDetail from "./New/components/PortfolioDetail";
import Contact from "./New/Contact";
// import Landing from "./Pages/Landing";

function App() {
  return (
    <BrowserRouter>
      {/* <Route path="/" component={Landing}></Route> */}
    
      <Route exact path={`${process.env.PUBLIC_URL}/`} component={NewLanding}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/about`} component={About}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/resume`} component={Resume}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/services`} component={Services}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/portfolio`} component={Portfolio}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/portfolio/detail`} component={PortfolioDetail}></Route>
      <Route exact path={`${process.env.PUBLIC_URL}/contact`} component={Contact}></Route>
    </BrowserRouter>
  );
}

export default App;
